* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  background: rgb(172, 99, 246);
}

h1 {
  margin: 100px auto 50px;
  text-align: center;
}

.App {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 600px;
}

form {
  display: flex;
  justify-content: flex-start;
}

.todo-input {
  background: rgb(184, 146, 222);
  margin-bottom: 20px;
  height: 40px;
  width: 300px;
  padding: 0 12px;
  border: none;
  box-shadow: 2px 4px 6px rgb(69, 13, 77);
  border-bottom-left-radius: 6px;
  border-top-left-radius: 6px;
}

.todo-input::placeholder {
  color: rgb(143, 81, 151);
}

.todo-input:focus {
  outline-color: rgb(233, 133, 3);
  outline-width: 2px;
  outline-offset: -2px; 
  outline-style: solid;
}

.todo-button {
  height: 40px;
  width: 80px;
  border-bottom-right-radius: 6px;
  border-top-right-radius: 6px;
  border: none;
  box-shadow: 2px 4px 6px rgb(69, 13, 77);
  background-color: rgb(233, 133, 3);
  color:  rgb(243, 210, 48);
  font-size: 18px;
}

.todo-button:focus {
  outline-color: rgb(243, 210, 48);
  outline-width: 2px;
  outline-offset: -2px; 
  outline-style: solid;
}

.todo-button:active {
  outline: none;
  box-shadow: 
  inset 3px 3px 5px rgb(170, 97, 1),
  inset -3px -3px 5px rgb(170, 97, 1);
  color: rgb(170, 97, 1);
}

.complete-btn {
  height: 40px;
  width: 40px;
  background-color: rgb(33, 114, 134);
  box-shadow: 2px 4px 6px rgb(69, 13, 77);
  border: none;
  color:  rgb(122, 245, 245);
  font-size: 18px;
}

.complete-btn:focus {
  outline-color: rgb(31, 178, 180);
  outline-width: 2px;
  outline-offset: -2px; 
  outline-style: solid;
}

.complete-btn:active {
  outline: none;
  box-shadow: 
  inset 3px 3px 5px rgb(11, 73, 89),
  inset -3px -3px 5px rgb(11, 73, 89);
  color: rgb(11, 73, 89);
}

.trash-btn {
  height: 40px;
  width: 40px;
  border-bottom-right-radius: 6px;
  border-top-right-radius: 6px;
  box-shadow: 2px 4px 6px rgb(69, 13, 77);
  border: none;
  background-color: rgb(140, 14, 219);
  color:  rgb(212, 155, 226);
  font-size: 18px;
}

.trash-btn:focus {
  outline-color: rgb(180, 68, 195);
  outline-width: 2px;
  outline-offset: -2px; 
  outline-style: solid;

}

.trash-btn:active {
  outline: none;
  box-shadow: 
  inset 3px 3px 5px rgb(91, 18, 100),
  inset -3px -3px 5px rgb(91, 18, 100);
  color: rgb(91, 18, 100);
}

.filter-todo {
  height: 40px;
  width: 150px;
  background: rebeccapurple;
  border-radius: 6px;
  color: plum;
  padding: 0 12px;
  border: none;
  box-shadow: 2px 4px 6px rgb(69, 13, 77);
  margin-left: 30px;
}

.filter-todo:focus {
  outline: none;
}

.todo {
  margin-top: 10px;
  display: flex;
  justify-content: flex-start;
}

.todo-item {
  width: 200px;
  height: 40px;
  background-color: aquamarine;

  background: rgb(184, 146, 222);
  height: 40px;
  width: 300px;
  padding: 0 12px;
  border: none;
  box-shadow: 2px 4px 6px rgb(69, 13, 77);
  border-bottom-left-radius: 6px;
  border-top-left-radius: 6px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
}

.completed {
  text-decoration: line-through;
  opacity: 0.5;
}

h1 {
  color: rgb(60, 9, 119);
}